import React, { useEffect, useState } from "react";
import axios from "axios";
import { Cookies } from "react-cookie";
import { Link, useNavigate } from "react-router-dom";
import PayBox from "./PayBox";
import PayBtn from "./PayBtn";

const cookies = new Cookies();

const CartPage = () => {
  const navigate = useNavigate();
  const [noCart, setNocart] = useState(false);
  const [del, setDel] = useState(false);
  const [items, setItems] = useState([]);
  const [quantity, setQuantity] = useState([]);
  const [price, setPrice] = useState(0);
  const [pay, setPay] = useState(false);
  const [email, setEmail] = useState("");
  const token = cookies.get(["auth"]);
  useEffect(() => {
    axios({
      method: "POST",
      url: `http://localhost:8080/cart/cartview`,
      withCredentials: true,
      data: {
        token,
      },
    })
      .then((res) => {
        setDel(false);
        if (!res.data.cart) {
          setItems([]);
          setNocart(true);
          setPay(false);
        } else {
          setPay(true);
          setItems(res.data.cart[0]);
          setQuantity(res.data.quantity);
          setEmail(res.data.email);
          let calculateTotal = () => {
            let total = 0;
            res.data.cart[0].map((item) => {
              res.data.quantity.map((item02) => {
                if (item.no === item02.no) {
                  total += parseInt(item.price) * item02.quantity;
                }
              });
            });
            setPrice(total);
          };
          calculateTotal();
        }
      })
      .catch((err) => {
        navigate("/500");
      });
  }, [del]);
  const deleteItem = (no) => {
    axios({
      method: "POST",
      url: `http://localhost:8080/cart/del`,
      withCredentials: true,
      data: {
        no,
        token,
      },
    })
      .then((res) => {
        if (res.data.delete) setDel(true);
        else return alert("   .");
      })
      .catch((err) => {
        navigate("/500");
      });
  };
  return (
    <div className="cart userPage">
      <div className="container">
        <div className="titleBox">
          <h1></h1>
        </div>
        <div className="cartList">
          <ul className="tableTitle">
            <li className="idx">
              <span>No.</span>
            </li>
            <li>
              <span></span>
            </li>
            <li>
              <span></span>
            </li>
            <li>
              <span></span>
            </li>
            <li>
              <span></span>
            </li>
          </ul>
          {items.map((item, idx) => {
            return (
              <ul className="tableTitle" key={idx}>
                <li className="idx">
                  <span>{idx + 1}</span>
                </li>
                <li>
                  <div className="itemDesc">
                    <Link to={`/detail/${item.no}`}>
                      <img src={item.image[0]} alt={item.title} />
                    </Link>
                    <Link to={`/detail/${item.no}`}>
                      <span>{item.title}</span>
                    </Link>
                  </div>
                </li>
                <li>
                  <span>
                    {quantity.map((item02) => {
                      return item02.no === item.no ? item02.quantity : null;
                    })}
                  </span>
                </li>
                <li>
                  <span>{item.price.toLocaleString()}</span>
                </li>
                <li>
                  <button onClick={() => deleteItem(item.no)}>
                    <span></span>
                  </button>
                </li>
              </ul>
            );
          })}
          {noCart && (
            <div className="noCart">
              <p>   .</p>
            </div>
          )}
        </div>
        {pay && (
          <>
            <PayBox price={price} />
            <PayBtn price={price} email={email} />
          </>
        )}
      </div>
    </div>
  );
};

export default CartPage;
