import React, { useEffect, useRef, useState } from "react";
import axios from "axios";
import { Cookies } from "react-cookie";
import { useNavigate } from "react-router-dom";
import "../../scss/user.scss";
import { useDaumPostcodePopup } from "react-daum-postcode";
const cookies = new Cookies();

const Mypage = () => {
  const open = useDaumPostcodePopup("//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js");
  const token = cookies.get(["auth"]);
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [tel, setTel] = useState("");
  const [address02, setAddress02] = useState("");
  const [zipCode, setZipcode] = useState("");
  const [address01, setAddress01] = useState("");
  const emailFocus = useRef();

  useEffect(() => {
    axios({
      url: "http://localhost:8080/user/mypage",
      method: "POST",
      data: { token },
    })
      .then((res) => {
        setEmail(res.data.user.email);
        setName(res.data.user.name);
        setTel(res.data.user.tel);
        setZipcode(res.data.user.zipCode);
        setAddress01(res.data.user.address01);
        setAddress02(res.data.user.address02);
      })
      .catch((err) => navigate("/500"));
  }, []);

  const inputName = (e) => {
    setName(e.target.value);
  };
  const inputPassword = (e) => {
    setPassword(e.target.value);
  };
  const inputTel = (e) => {
    const regex = /^[0-9\b ]{0,13}$/;
    if (regex.test(e.target.value)) {
      setTel(e.target.value);
    }
  };
  const inputAddress02 = (e) => {
    setAddress02(e.target.value);
  };

  const reset = (e) => {
    navigate("/");
  };

  const editInfo = (e) => {
    e.preventDefault();
    if (password === "") return alert(" ");
    if (name === "") return alert(" .");
    if (tel === "") return alert(" .");
    if (zipCode === "") return alert(" .");
    if (address01 === "") return alert(" .");

    axios({
      url: "http://localhost:8080/user/editinfo",
      method: "POST",
      data: {
        token,
        email,
        password,
        name,
        tel,
        zipCode,
        address01,
        address02,
      },
    })
      .then((res) => {
        if (res.data.message === " .") return alert(`${res.data.message}`);
        if (res.data.edit) {
          alert("  .");
          navigate("/");
        }
      })
      .catch((err) => {
        navigate("/500");
      });
  };

  // daum-post
  const handleComplete = (data) => {
    let fullAddress = data.address;
    let extraAddress = "";

    if (data.addressType === "R") {
      if (data.bname !== "") {
        extraAddress += data.bname;
      }
      if (data.buildingName !== "") {
        extraAddress += extraAddress !== "" ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress !== "" ? ` (${extraAddress})` : "";
    }
    setZipcode(data.zonecode);
    setAddress01(data.address);
  };

  const handleClick = (e) => {
    e.preventDefault();
    open({ onComplete: handleComplete });
  };

  return (
    <div className="mypage userPage">
      <div className="container">
        <div className="titleBox">
          <h1>  </h1>
        </div>
        <div className="registerBox">
          <form action="" className="mypageForm">
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="email" name="email" placeholder="Email" required value={email} readOnly />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="password" name="password" required placeholder="Password (8~16)" value={password} onChange={inputPassword} maxLength={16} minLength={8} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="text" name="name" placeholder="Name" required value={name} onChange={inputName} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="tel" name="tel" placeholder="- " maxLength={13} required value={tel} onChange={inputTel} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner addressBox">
                <div className="zipCodeBox">
                  <input type="text" name="zipCode" required readOnly placeholder="Zip code" value={zipCode} />
                  <button className="btn" onClick={handleClick}>
                     
                  </button>
                </div>
                <input type="text" name="address01" required readOnly value={address01} />
                <input type="text" name="address02" value={address02} onChange={inputAddress02} />
              </div>
            </div>
            <div className="btns">
              <button className="btn joinBtn" onClick={editInfo}>
                
              </button>
              <button className="btn resetBtn" onClick={reset}>
                
              </button>
              <button className="btn signOutBtn"></button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Mypage;
