import axios from "axios";
import React, { useEffect, useRef, useState } from "react";
import KakaoLoginBtn from "./KakaoLoginBtn";
import { useNavigate } from "react-router-dom";

const Login = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const emailFocus = useRef();
  //  
  const inputEmail = (e) => {
    setEmail(e.target.value);
  };
  const inputPassword = (e) => {
    setPassword(e.target.value);
  };
  //       
  const reset = (e) => {
    e.preventDefault();
    setEmail("");
    setPassword("");
    emailFocus.current.focus();
  };
  //   
  useEffect(() => {
    emailFocus.current.focus();
  }, []);
  // 
  const loginFunc = () => {
    if (email === "") return alert(" .");
    if (password === "") return alert(" .");
    axios({
      method: "POST",
      url: "http://localhost:8080/user/login",
      withCredentials: true,
      data: {
        email,
        password,
      },
    })
      .then((res) => {
        if (res.data.message === "  .") return alert("  .");
        if (res.data.message === " .") return alert(" .");
        if (res.data.login) {
          window.location.replace("/");
        }
      })
      .catch((err) => {
        navigate("/500");
      });
  };
  const loginClickEvent = (e) => {
    e.preventDefault();
    loginFunc();
  };
  const loginKeyEvent = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      return loginFunc();
    }
  };
  return (
    <div className="login userPage">
      <div className="container">
        <div className="titleBox">
          <h1></h1>
        </div>
        <div className="loginBox">
          <form className="loginForm">
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="email" name="email" value={email} onChange={inputEmail} ref={emailFocus} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="password" name="password" value={password} onChange={inputPassword} onKeyDown={loginKeyEvent} maxLength={16} minLength={8} />
              </div>
            </div>
            <KakaoLoginBtn />
            <div className="btns">
              <button className="btn loginBtn" onClick={loginClickEvent}>
                
              </button>
              <button className="btn resetBtn" onClick={reset}>
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Login;
