import React, { useEffect, useRef, useState } from "react";
import "../../scss/user.scss";
import { useDaumPostcodePopup } from "react-daum-postcode";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const Register = () => {
  const open = useDaumPostcodePopup("//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js");
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [emailReturn, setEmailReturn] = useState(false);
  const [password, setPassword] = useState("");
  const [pwCheck, setPwCheck] = useState("");
  const [tel, setTel] = useState("");
  const [address02, setAddress02] = useState("");
  const [zipCode, setZipcode] = useState("");
  const [address01, setAddress01] = useState("");
  const emailFocus = useRef();

  useEffect(() => {
    emailFocus.current.focus();
  }, []);
  //   
  const inputName = (e) => {
    setName(e.target.value);
  };
  const inputEmail = (e) => {
    setEmailReturn(false);
    setEmail(e.target.value);
  };
  const inputPassword = (e) => {
    setPassword(e.target.value);
  };
  const inputPwCheck = (e) => {
    setPwCheck(e.target.value);
  };
  const inputTel = (e) => {
    const regex = /^[0-9\b ]{0,13}$/;
    if (regex.test(e.target.value)) {
      setTel(e.target.value);
    }
  };
  const inputAddress02 = (e) => {
    setAddress02(e.target.value);
  };
  const reset = (e) => {
    e.preventDefault();
    setName("");
    setEmail("");
    setPassword("");
    setPwCheck("");
    setTel("");
    setZipcode("");
    setAddress01("");
    setAddress02("");
    emailFocus.current.focus();
  };
  //    
  const emailCheck = (e) => {
    e.preventDefault();
    var regExp = /^([0-9a-zA-Z_\.-]+)@([0-9a-zA-Z_-]+)(\.[0-9a-zA-Z_-]+){1,2}$/;
    if (regExp.test(email)) {
      axios({
        url: "http://localhost:8080/user/emailCheck",
        method: "POST",
        data: {
          email: email,
        },
      })
        .then((res) => {
          if (res.data.emailCheck) {
            setEmailReturn(true);
            alert("   .");
          } else {
            alert("  .");
          }
        })
        .catch((err) => {
          navigate("/500");
        });
    } else {
      alert("    .");
    }
  };
  // 
  const fetchJoin = (e) => {
    e.preventDefault();
    if (email === "") return alert(" .");
    if (password === "" || pwCheck === "") return alert(" ");
    if (name === "") return alert(" .");
    if (tel === "") return alert(" .");
    if (zipCode === "") return alert(" .");
    if (address01 === "") return alert(" .");
    if (emailReturn) {
      if (password === pwCheck) {
        axios({
          url: "http://localhost:8080/user/join",
          method: "POST",
          data: {
            email,
            password,
            name,
            tel,
            zipCode,
            address01,
            address02,
          },
        })
          .then((res) => {
            if (res.data.join) {
              alert(".  .");
              navigate("/");
            }
          })
          .catch((err) => {
            navigate("/500");
          });
      } else return alert("  ");
    } else {
      alert("   .");
    }
  };
  // daum-post
  const handleComplete = (data) => {
    let fullAddress = data.address;
    let extraAddress = "";

    if (data.addressType === "R") {
      if (data.bname !== "") {
        extraAddress += data.bname;
      }
      if (data.buildingName !== "") {
        extraAddress += extraAddress !== "" ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress !== "" ? ` (${extraAddress})` : "";
    }
    setZipcode(data.zonecode);
    setAddress01(data.address);
  };

  const handleClick = (e) => {
    e.preventDefault();
    open({ onComplete: handleComplete });
  };
  return (
    <div className="register userPage">
      <div className="container">
        <div className="titleBox">
          <h1></h1>
        </div>
        <div className="registerBox">
          <form action="" className="registerForm">
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="email" name="email" placeholder="Email" required value={email} onChange={inputEmail} ref={emailFocus} />
                <button className="btn" onClick={emailCheck}>
                    
                </button>
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="password" name="password" required placeholder="Password (8~16)" value={password} onChange={inputPassword} maxLength={16} minLength={8} />
              </div>
            </div>
            <div className="inputBox">
              <label> </label>
              <div className="inner">
                <input type="password" name="passwordCheck" required placeholder="Password check (8~16)" value={pwCheck} onChange={inputPwCheck} maxLength={16} minLength={8} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="text" name="name" placeholder="Name" required value={name} onChange={inputName} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner">
                <input type="tel" name="tel" placeholder="- " maxLength={13} required value={tel} onChange={inputTel} />
              </div>
            </div>
            <div className="inputBox">
              <label></label>
              <div className="inner addressBox">
                <div className="zipCodeBox">
                  <input type="text" name="zipCode" required readOnly placeholder="Zip code" value={zipCode} />
                  <button className="btn" onClick={handleClick}>
                     
                  </button>
                </div>
                <input type="text" name="address01" required readOnly value={address01} />
                <input type="text" name="address02" value={address02} onChange={inputAddress02} />
              </div>
            </div>
            <div className="btns">
              <button className="btn joinBtn" onClick={fetchJoin}>
                
              </button>
              <button className="btn resetBtn" onClick={reset}>
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Register;
